___CSS_LOADER_EXPORT___.push([module.id, ".container-fluid[data-v-e85a4276]{padding:0!important}.router-link-active[data-v-e85a4276]{background-color:#3490dc;border-radius:10px;color:#fff!important;text-decoration:none}", "",{"version":3,"sources":["webpack://./resources/js/components/Navbar/NavbarComponent.vue"],"names":[],"mappings":"AACA,kCACA,mBACA,CACA,qCAGA,wBAAA,CACA,kBAAA,CAHA,oBAAA,CACA,oBAGA","sourcesContent":["<style scoped>\r\n    .container-fluid {\r\n        padding: 0px !important;\r\n    }\r\n    .router-link-active {\r\n        color: #fff !important;\r\n        text-decoration: none;\r\n        background-color: #3490dc;\r\n        border-radius: 10px;\r\n    }\r\n</style>\r\n<template>\r\n    <div class=\"container-fluid\">\r\n        <nav class=\"navbar navbar-expand-lg navbar-dark bg-dark\">\r\n            <router-link to=\"/\" class=\"navbar-brand\">\r\n                EventosJ\r\n            </router-link>\r\n            <button class=\"navbar-toggler\" type=\"button\" data-toggle=\"collapse\" data-target=\"#navbarSupportedContent\" aria-controls=\"navbarSupportedContent\" aria-expanded=\"false\" aria-label=\"Toggle navigation\">\r\n                <span class=\"navbar-toggler-icon\"></span>\r\n            </button>\r\n\r\n            <div class=\"collapse navbar-collapse\" id=\"navbarSupportedContent\">\r\n                <ul class=\"navbar-nav mr-auto\">\r\n                    <li class=\"nav-item dropdown\">\r\n                        <a class=\"nav-link dropdown-toggle\" href=\"#\" id=\"navbarDropdown\" role=\"button\" data-toggle=\"dropdown\" aria-haspopup=\"true\" aria-expanded=\"false\" :class=\"{'router-link-active': subIsActive('/eventos')}\">\r\n                        Eventos\r\n                        </a>\r\n                        <div class=\"dropdown-menu\" aria-labelledby=\"navbarDropdown\">\r\n                            <router-link to=\"/eventos\" class=\"dropdown-item\" exact>\r\n                                Todos os Eventos\r\n                            </router-link>\r\n                            <router-link to=\"/eventos/create\" class=\"dropdown-item\" exact>\r\n                                Criar Evento\r\n                            </router-link>\r\n                        </div>\r\n                    </li>\r\n                    <li class=\"nav-item dropdown\">\r\n                        <a class=\"nav-link dropdown-toggle\" href=\"#\" id=\"navbarDropdown\" role=\"button\" data-toggle=\"dropdown\" aria-haspopup=\"true\" aria-expanded=\"false\" :class=\"{'router-link-active': subIsActive('/convidados')}\">\r\n                        Convidados\r\n                        </a>\r\n                        <div class=\"dropdown-menu\" aria-labelledby=\"navbarDropdown\">\r\n                            <router-link to=\"/convidados\" class=\"dropdown-item\" exact>\r\n                                Todos os Convidados\r\n                            </router-link>\r\n                            <router-link to=\"/convidados/create\" class=\"dropdown-item\" exact>\r\n                                Criar Convidado\r\n                            </router-link>\r\n                        </div>\r\n                    </li>\r\n                </ul>\r\n            </div>\r\n        </nav>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\n    export default {\r\n        mounted() {\r\n        },\r\n        methods: {\r\n            /**\r\n             * Mtodo responsvel por averiguar se um dos itens filhos do dropdown \r\n             * est com a class 'active'.\r\n             * Se o estiver, a coloque no pai tambm.\r\n             */\r\n            subIsActive(input) {\r\n                const paths = Array.isArray(input) ? input : [input];\r\n                return paths.some(path => {\r\n                    return this.$route.path.indexOf(path) === 0 // current path starts with this path string\r\n                });\r\n            }\r\n        }\r\n    }\r\n</script>\r\n"],"sour